export default {
  props: {
    isBack: {
      type: Boolean,
      default: true
    },

    message: {
      type: String,
      default: '啥也没有 /(ㄒoㄒ)/~~'
    }
  },

  methods: {
    back() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}
