import { mapMutations } from 'vuex'

export default {
  props: {
    size: {
      type: String,
      default: "30px"
    }
  },

  computed: {
    theme () {
      return this.$store.state.app.theme
    }
  },

  methods: {
    switchTheme() {
      if (this.theme === 'light') {
        this.setTheme('dark')
      } else {
        this.setTheme('light')
      }
    },

    ...mapMutations({
      setTheme: 'app/setTheme'
    })
  },
};
