import {mapMutations,mapState} from "vuex";
import ClickOutside from "@/services/directives/click-outside";
import ClientOnly from '@/components/base/client-only/client-only'
import dropRight from 'lodash/dropRight'

export default {
    computed: {
        ...mapState({
            navList: state => state.app.navList,
            clientNavList: state => state.app.clientNavList,
            token: state => state.app.token,
        })
    },
    data() {
        return {
            isShowMobileNav: false
        };
    },
    components: {
        ClientOnly
    },
    methods: {
        hideMobileNav(){
            this.isShowMobileNav=false
        },
        showSearch() {
            this.setShowSearch(true)
        },

        hide() {
            this.isShowMobileNav = false
        },

        ...mapMutations({
            setShowSearch: 'app/setShowSearch'
        })
    },

    mounted() {
    },

    directives: {
        ClickOutside
    }
};
